<template>
    <div class="about">
        <img src="../../assets/us.png" class="img"/>
        <div class="navigation">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7" @click="navigator('/')">{{ $t('home') }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">{{ $t('contactUs') }}</div>
        </div>
        <div class="news">
            <div class="company-news">{{ $t('contactUs') }}</div>
            <div class="company-en">CONTACT US</div>
            <div class="card">
                <div class="concat">
                    <div class="company-name">{{ info.company_name }}</div>
                    <div class="company-name-en">{{ info.company_english }}</div>
                    <div class="rows" style="margin-top: 34px;">
                        <img src="../../assets/about/icon3.png" class="icons"/>
                        {{ $t('phone') }}： {{ info.contact.phone || info.contact.telephone }}
                    </div>
                    <div class="rows" style="margin-top: 23px;">
                        <img src="../../assets/about/icon2.png" class="icons"/>
                        {{ $t('email') }}：{{ info.contact.email }}
                    </div>
                    <div class="rows" style="margin-top: 23px;">
                        <img src="../../assets/about/icon1.png" class="icons"/>
                        {{ $t('address') }}：{{ info.contact.address }}
                    </div>
                    <img v-if="info.WxCode" :src="info.WxCode" class="WxCode"/>
                </div>
                <div class="map" ref="mapContainer"></div>
            </div>
            <div class="concat-i">
                <div class="c-title">{{ $t('onlineMessage') }}</div>
                <div class="c-title-en">ONLINE MESSAGE</div>
                <div class="us">
                    <div class="inputs">
                        <span style="color: #666666;">*</span>
                        <input v-model="message.name" :placeholder="$t('nickname')" class="input"/>
                    </div>
                    <div class="inputs">
                        <span style="color: #666666;">*</span>
                        <input v-model="message.phone" :placeholder="$t('contactInformationInput')" class="input"/>
                    </div>
                    <div class="inputs">
                        <input v-model="message.company" :placeholder="$t('company')" class="input"/>
                    </div>
                </div>
                <textarea :placeholder="$t('demand')" v-model="message.describe" class="textarea"></textarea>
                <button class="btn" @click="submit">{{ $t('submit') }}</button>
            </div>
        </div>
    </div>
</template>
<script setup>
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { ref,onMounted, computed, reactive } from 'vue';

    const store = useStore();
    const router = useRouter();
    const info = computed(() => store.state.info);
    document.title = `联系我们-${ info.value.company_name }`
    const cloud = store.state.cloud;
    let mapContainer = ref(null)
    let message = reactive({
        name: '',
        phone: '',
        company: '',
        describe: ''
    })

    const TMap = window.TMap

    const navigator = () => {
        router.push("/")
    }
    onMounted(() => {
        let longitude = 116.397590;
        let latitude = 39.908776;
        if (info.value.contact?.longitude) {
            longitude = info.value.contact.longitude;
            latitude = info.value.contact.latitude;
        }
        /* eslint-disable */
        const center = new TMap.LatLng(latitude, longitude)
        const map = new TMap.Map(mapContainer.value, {
            center: center, // 设置地图中心点坐标
            zoom: 15, // 设置地图缩放级别
            viewMode: '2D',
        })
        new TMap.MultiMarker({
            map: map,
            geometries: [{ position: center }],
        })
    })

    const submit = () => {
        if(!message.name) {
            alert('请输入您的称呼')
            return;
        }
        if(!message.phone) {
            alert('请输入您的联系方式')
            return;
        }
        cloud.callFunction({
            name: 'onlineMessageFunctions',
            data: {
                method: 'add',
                nickname: message.name,
                contact: message.phone,
                unit_name: message.company,
                demand: message.describe,
                website_id: info.value._id
            }
        }).then(() => {
            message.name = "";
            message.phone = "";
            message.company = "";
            message.describe = "";
            alert('提交成功')
        })
    }
</script>
